<template>
  <div class="h-full flex flex-col">
    <div ref="messages" class="flex-auto px-3 max-h-full overflow-y-auto">
      <p class="py-3 text-gray-600">This is the start of your magnificent chat with ygg.</p>
      <hr class="h-2">
      <div v-for="(line, i) of chat" :key="i" :class="textSide(line)">
        <transition appear>
          <div class="mt-2">
            <p v-if="line.sender" class="text-sm">@{{ line.sender }}</p>
            <p :class="textProps(line)">{{ line.message }}</p>
          </div>
        </transition>
      </div>
      <div class="h-10" />
    </div>
    <div class="flex-initial flex w-full bg-gray-400 p-3 rounded-b-lg">
      <div class="flex-auto rounded-sm bg-white">
        <input v-model="message" @keyup.enter="send" class="w-full px-3">
      </div>
      <button @click="send" class="flex-initial pl-3 text-purple-500 mdi mdi-send" />
    </div>
  </div>
</template>

<script>
import FakeWindow from './Window.vue'
export default {
  name: 'chat-app',
  data() {
    return {
      chat: [
        { sender: null, message: 'im listening to kpop' },
        { sender: null, message: 'and dying' },
        { sender: 'ygg', message: 'why are you dying' },
        { sender: null, message: 'its a constant mood' }
      ],
      message: ''
    }
  },
  methods: {
    scrollToEnd() {
      this.$nextTick(() => {
        let messages = this.$refs.messages
        messages.scrollTop = messages.scrollHeight
      })
    },
    send() {
      if (/\S/.test(this.message)) {
        this.chat.push({ sender: null, message: this.message })
        this.message = ''
        this.scrollToEnd()
      }
    },
    textSide(line) {
      return line.sender !== null ? 'flex justify-start' : 'flex justify-end'
    },
    textProps(line) {
      if (line.sender !== null) {
        return 'rounded-lg p-3 mr-20 bg-gray-400'
      } else {
        return 'rounded-lg p-3 ml-20 bg-purple-300'
      }
    }
  },
  created() {
    let saved = this.$root.appdata.chat
    if (saved) {
      this.chat = saved
    }
    this.scrollToEnd()
  },
  destroyed() {
    this.$root.appdata.chat = this.chat
  }
}
</script>