<template>
  <div class="px-3 h-full rounded-b-lg bg-purple-500 text-white">
    <div id="player" key="player" class="hidden"></div>
    <div class="flex align-center">
      <button
        @click="prevVideo"
        :disabled="hasPrevVideo"
        :class="{ 'text-purple-700 cursor-default': hasPrevVideo }"
      >
        <span class="mdi mdi-rewind text-2xl" />
      </button>
      <button v-show="!playing" @click="playVideo">
        <span class="mdi mdi-play text-4xl mx-2" />
      </button>
      <button v-show="playing" @click="pauseVideo">
        <span class="mdi mdi-pause text-4xl mx-2" />
      </button>
      <button
        @click="nextVideo"
        :disabled="hasNextVideo"
        :class="{ 'text-purple-700 cursor-default': hasNextVideo }"
      >
        <span class="mdi mdi-fast-forward text-2xl" />
      </button>
    </div>
    <p>{{ videos[currentVideo][1] }}</p>
  </div>
</template>

<script>
import FakeWindow from './Window.vue'
export default {
  name: 'music-app',
  data() {
    return {
      tag: null,
      player: null,
      playerLoaded: false,
      playing: false,
      videos: [
        ['7OUhho2s06k', 'Scars of Time - Chrono Cross'],
        ['EL0YNmt1q4E', 'Stayin\' Alive - Bee Gees (Vaporwave)'],
        ['rmDiNAIccW8', 'Black Rain - Creeper'],
        ['J67nkzoJ_2M', 'Stickerbrush Symphony - Donkey Kong Country 2'],
        ['i8wghCdMncU', 'Resuscitated Hope - Gosick'],
        ['d-nxW9qBtxQ', 'Ga1ahad and Scientific Witchery - Mili']
      ],
      currentVideo: 0
    }
  },
  computed: {
    hasPrevVideo() {
      return this.currentVideo < 1
    },
    hasNextVideo() {
      return this.currentVideo >= this.videos.length - 1
    }
  },
  methods: {
    loadPlayer() {
      this.player = new YT.Player('player', {
        height: '200',
        width: '200',
        videoId: this.videos[0][0],
        events: {
          onReady: () => {
            this.playerLoaded = true
          },
          onStateChange: (event) => {
            if (event.data == YT.PlayerState.ENDED) {
              this.playing = false
            }
          }
        }
      })
    },
    playVideo() {
      if (this.playerLoaded) {
        this.player.playVideo()
        this.playing = true
      }
    },
    pauseVideo() {
      this.player.pauseVideo()
      this.playing = false
    },
    nextVideo() {
      this.currentVideo++
      this.player.loadVideoById(this.videos[this.currentVideo][0])
      this.playing = true
    },
    prevVideo() {
      this.currentVideo--
      this.player.loadVideoById(this.videos[this.currentVideo][0])
      this.playing = true
    }
  },
  mounted() {
    if (!this.$root.appdata.music) this.$root.appdata.music = {}
    if (this.$root.appdata.music.apiLoaded) {
      this.loadPlayer()
    } else {
      this.tag = document.createElement('script');
      this.tag.src = "https://www.youtube.com/iframe_api";
      window.firstScriptTag = document.getElementsByTagName('script')[0];
      firstScriptTag.parentNode.insertBefore(this.tag, firstScriptTag);
      window.onYouTubeIframeAPIReady = () => {
        this.$root.appdata.music.apiLoaded = true
        this.loadPlayer()
      }
    }
  }
}
</script>

<style scoped>
iframe {
  position: absolute;
  width: 100%;
  height: 100%;
}
</style>