<template>
  <div class="flex h-full">
    <div class="flex-initial flex flex-col px-2 text-4xl text-gray-500">
      <button
        v-for="tool of tools"
        :key="tool.name"
        :class="tool.name == using.name ? 'text-purple-400' : ''"
        class="leading-snug"
        @click="toolButton(tool)"
      >
        <span :class="'mdi mdi-' + tool.icon" />
      </button>
      <button @click="newDocument">
        <span class="mdi mdi-file" />
      </button>
      <button
        v-for="col of colors"
        :key="col"
        @click="selectColor(col)"
        class="h-8 my-1 border"
        :class="{ 'border-purple-500': color == col }"
        :style="'background-color:' + col"
      />
      <a
        @click="save"
        :href="art"
        download="art.png"
        class="absolute bottom-0"
      >
        <span class="mdi mdi-floppy" />
      </a>
    </div>
    <div class="flex-auto bg-gray-200">
      <canvas
        ref="canvas"
        @mousedown="mouseDown"
        @mousemove="mouseMove"
      />
    </div>
    <popup-card :active="options" @close="toggleOptions">
      <p>Tool Options <span :class="'mdi mdi-' + using.icon" /></p>
      <div class="flex pt-2">
        <button @click="decreaseSize(using)">
          <span class="mdi mdi-minus" />
        </button>
        <input
          class="w-40 mx-2"
          type="range"
          min="1"
          :max="using.max"
          v-model="using.size"
        />
        <button @click="increaseSize(using)">
          <span class="mdi mdi-plus" />
        </button>
        <p class="w-10">{{ using.size }}px</p>
      </div>
    </popup-card>
  </div>
</template>

<script>
import PopupCard from './PopupCard.vue'
export default {
  components: {
    PopupCard
  },
  data() {
    return {
      tools: [
        {
          name: 'paintbrush',
          icon: 'brush',
          size: 2,
          max: 100
        },
        {
          name: 'pixel',
          icon: 'checkbox-blank',
          size: 1,
          max: 100
        },
        {
          name: 'glitter',
          icon: 'creation',
          size: 50,
          max: 200
        }
      ],
      colors: [
        // https://lospec.com/palette-list/darkboy4
        '#ffffff',
        '#e7e8f3',
        '#8c83c3',
        '#634d8f',
        '#120b19'
      ],
      using: null,
      color: '',
      canvas: null,
      ctx: null,
      mouse: {
        prevX: 0,
        prevY: 0,
        x: 0,
        y: 0
      },
      art: '',
      skipFrame: true,
      options: false
    }
  },
  methods: {
    toolButton(tool) {
      if (tool == this.using) {
        this.toggleOptions()
      } else {
        this.selectTool(tool)
      }
    },
    selectTool(tool) {
      this.using = tool
      let ctx = this.ctx
      ctx.lineWidth = tool.size
      ctx.strokeStyle = this.color
      ctx.fillStyle = this.color
      switch(tool.name) {
        case 'paintbrush':
          ctx.lineCap = 'round'
          break
        case 'pixel':
          ctx.lineCap = 'square'
          break
      }
    },
    selectColor(color) {
      this.color = color
      this.selectTool(this.using)
    },
    increaseSize(tool) {
      tool.size++
      if (tool.size > tool.max) tool.size = tool.max
    },
    decreaseSize(tool) {
      tool.size--
      if (tool.size < 1) tool.size = 1
    },
    toggleOptions() {
      this.options = !this.options
      if (!this.options) {
        this.selectTool(this.using)
      }
    },
    updateMouse(x, y) {
      let offsets = this.canvas.getBoundingClientRect()
      this.mouse.x = x - offsets.left
      this.mouse.y = y - offsets.top
    },
    mouseDown(e) {
      if (!this.options) {
        this.updateMouse(e.clientX, e.clientY)
        this.ctx.beginPath()
        switch (this.using.name) {
          case 'paintbrush':
            this.ctx.arc(this.mouse.x, this.mouse.y, this.using.size/2, 0, 2*Math.PI)
            break
          case 'pixel':
            this.ctx.fillRect(this.mouse.x, this.mouse.y, this.using.size, this.using.size)
            break
          case 'glitter':
            this.drawGlitter(this.mouse.x, this.mouse.y)
            break
        }
        this.ctx.fill()
        this.ctx.closePath()
      }
    },
    mouseMove(e) {
      if (e.buttons) {
        let prevX = this.mouse.x
        let prevY = this.mouse.y
        this.updateMouse(e.clientX, e.clientY)
        if (this.skipFrame) {
          this.skipFrame = false
        } else {
          // draw
          let ctx = this.ctx
          if (this.using.name == 'glitter') {
            this.drawGlitter(this.mouse.x, this.mouse.y)
          } else {
            ctx.beginPath()
            ctx.moveTo(prevX, prevY)
            ctx.lineTo(this.mouse.x, this.mouse.y)
            ctx.stroke()
            ctx.closePath()
          }
        }
      } else {
        this.skipFrame = true
      }
    },
    drawGlitter(x, y) {
      let offsetx = 0, offsety = 0
      for (var i = 0; i < this.using.size; i++) {
        offsetx = Math.random() * i
        offsety = Math.random() * i
        this.ctx.fillRect(x + offsetx, y + offsety, 1, 1)
        this.ctx.fillRect(x + offsetx, y - offsety, 1, 1)
        this.ctx.fillRect(x - offsetx, y - offsety, 1, 1)
        this.ctx.fillRect(x - offsetx, y + offsety, 1, 1)
      }
    },
    newDocument() {
      let canvas = this.canvas
      let ctx = this.ctx
      canvas.width = canvas.parentNode.offsetWidth
      canvas.height = canvas.parentNode.offsetHeight
      ctx.fillStyle = this.colors[1]
      ctx.fillRect(0, 0, canvas.width, canvas.height)
    },
    save() {
      this.art = this.canvas.toDataURL("image/png")
    }
  },
  created() {
    this.using = this.tools[0]
  },
  mounted() {
    this.canvas = this.$refs.canvas
    this.ctx = this.canvas.getContext('2d')
    // waiting an extra tick...for flexbox?
    this.$nextTick(() => { this.$nextTick(() => {
      this.newDocument()
      this.color = this.colors[this.colors.length - 1]
      this.art = this.canvas.toDataURL("image/png")
      this.selectTool(this.tools[0])
    })})
  }
}
</script>