<template>
  <div class="absolute bottom-0 flex justify-center w-full bg-gray-700 text-white text-4xl">
    <button
      v-for="app of apps"
      :key="app.name + 'ico'"
      @click="launchApp(app)"
      class="px-5"
      :class="{ 'bg-gray-500': app.open }"
    >
      <span :class="'mdi mdi-' + app.icon" />
    </button>
  </div>
</template>

<script>
export default {
  name: 'task-bar',
  props: {
    apps: {
      type: Array,
      required: true
    }
  },
  methods: {
    launchApp(app) {
      if (!app.open) {
        app.open = true
        this.$emit('window-launch', app)
      } else if (app.minimized) {
        app.minimized = false
        this.$emit('window-focus', app)
      } else {
        this.$emit('window-hide', app)
      }
    }
  }
}
</script>