<template>
  <div id="app" style="width: 1050px; height: 750px;">
    <task-bar
      :apps="sortedApps"
      @window-launch="windowLaunch"
      @window-hide="windowHide"
      @window-focus="windowFocus"
    />
    <fake-window
      v-for="(win, i) of openWindows"
      :key="win.name"
      v-show="!win.minimized"
      :name="win.name"
      :xpos="win.xpos"
      :ypos="win.ypos"
      :width="win.width"
      :height="win.height"
      :min-width="win.width"
      :min-height="win.height"
      :resizable="win.resizable"
      @window-focus="windowFocus(win)"
      @window-hide="windowHide(win)"
      @window-close="windowClose(i)"
      @window-save-pos="windowSavePos(win, $event)"
      class="relative"
      :style="{ zIndex: win.z }"
    >
    <component :is="win.name + '-app'" />
    </fake-window>
  </div>
</template>

<script>
import TaskBar from './TaskBar.vue'
import FakeWindow from './Window.vue'
import PaintApp from './Paint.vue'
import NotepadApp from './Notepad.vue'
import GameApp from './Game.vue'
import ChatApp from './Chat.vue'
import MusicApp from './Music.vue'
export default {
  name: 'app',
  components: {
    TaskBar,
    FakeWindow,
    PaintApp,
    NotepadApp,
    GameApp,
    ChatApp,
    MusicApp
  },
  data() {
    return {
      apps: {
        paint: {
          name: 'paint',
          icon: 'brush',
          xpos: 400,
          ypos: 100,
          width: 500,
          height: 500,
          z: 0,
          minimized: false,
          open: true
        },
        notepad: {
          name: 'notepad',
          icon: 'file-document-outline',
          xpos: 200,
          ypos: 370,
          width: 400,
          height: 300,
          z: 0,
          minimized: false,
          open: true
        },
        game: {
          name: 'game',
          icon: 'gamepad-square',
          xpos: 290,
          ypos: 130,
          width: 640,
          height: 480,
          z: 0,
          minimized: false,
          open: false,
          resizable: false
        },
        music: {
          name: 'music',
          icon: 'music-box-multiple',
          xpos: 20,
          ypos: 30,
          width: 300,
          height: 150,
          z: 0,
          minimized: false,
          open: true
        },
        chat: {
          name: 'chat',
          icon: 'message',
          xpos: 40,
          ypos: 50,
          width: 300,
          height: 500,
          z: 0,
          minimized: false,
          open: true
        }
      },
      sortedApps: [],
      openWindows: []
    }
  },
  created() {
    this.sortedApps = [this.apps.chat, this.apps.music, this.apps.paint, this.apps.notepad, this.apps.game]
    for (let app of this.sortedApps) {
      if (app.open) {
        this.windowLaunch(app)
      }
    }
  },
  methods: {
    windowLaunch(app) {
      app.z = this.openWindows.length
      this.openWindows.push(app)
    },
    windowHide(app) {
      app.minimized = true
    },
    windowFocus(app) {
      for (let win of this.openWindows) {
        if (win.z > app.z) {
          win.z--
        }
      }
      app.z = this.openWindows.length
    },
    windowClose(index) {
      let closed = this.openWindows.splice(index, 1)
      closed[0].open = false
    },
    windowSavePos(app, {x, y}) {
      app.xpos = x
      app.ypos = y
    }
  }
}
</script>

<style scoped>
#app {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
</style>