<template>
  <vue-draggable-resizable
    :x="x"
    :y="y"
    :w="w"
    :h="h"
    :min-width="minWidth"
    :resizable="resizable"
    :min-height="minHeight"
    :drag-handle="handle"
    @activated="focusWindow"
    @deactivated="unfocusWindow"
    @dragging="moveWindow"
    @resizing="resizeWindow"
    parent
    class="rounded-lg shadow-lg bg-white flex flex-col select-none"
  >
    <div class="header flex-none relative bg-gray-700 rounded-t h-6 text-white">
      <p class="absolute left-0 pl-2">{{ name }}</p>
      <div class="absolute right-0 pr-2">
        <button @click="minWindow" class="mdi mdi-window-minimize" />
        <button @click="maxWindow" class="mdi mdi-window-maximize px-2" />
        <button @click="closeWindow" class="mdi mdi-close-circle" />
      </div>
    </div>
    <div class="flex-auto overflow-hidden relative">
      <slot />
    </div>
  </vue-draggable-resizable>
</template>

<script>
import VueDraggableResizable from 'vue-draggable-resizable'
export default {
  components: {
    'vue-draggable-resizable': VueDraggableResizable
  },
  props: {
    name: String,
    xpos: {
      type: Number,
      default: 0
    },
    ypos: {
      type: Number,
      default: 0
    },
    width: {
      type: Number,
      default: 200
    },
    height: {
      type: Number,
      default: 200
    },
    minWidth: {
      type: Number,
      default: 200
    },
    minHeight: {
      type: Number,
      default: 200
    },
    resizable: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      w: this.width,
      h: this.height,
      x: this.xpos,
      y: this.ypos,
      handle: undefined
    }
  },
  methods: {
    focusWindow() {
      this.$emit('window-focus')
      this.$nextTick(() => {
        this.handle = '.header'
      })
    },
    unfocusWindow() {
      this.handle = undefined
    },
    resizeWindow(x, y, w, h) {
      this.x = x
      this.y = y
      this.w = w
      this.h = h
    },
    moveWindow(x, y) {
      this.x = x
      this.y = y
    },
    minWindow() {
      this.$emit('window-hide')
    },
    maxWindow() {
      if (this.x == 0 && this.y == 0) {
        this.w = this.width
        this.h = this.height
        this.$nextTick(() => {
          this.x = this.xpos
          this.y = this.ypos
        })
      } else {
        this.$emit('window-save-pos', {x: this.x, y: this.y})
        this.x = 0
        this.y = 0
        this.w = window.innerWidth
        this.h = window.innerHeight
      }
    },
    closeWindow() {
      this.$emit('window-close')
    }
  }
}
</script>

<style>
.vdr {
  border: none;
}
.handle {
  opacity: 0;
}
.handle-tm,
.handle-bm {
  width: 100%;
  left: 5px;
}
.handle-mr,
.handle-ml {
  height: 100%;
  top: 5px;
}
</style>