<template>
  <textarea
    v-model="text"
    class="w-full p-3 rounded-b-lg bg-pink-200"
    style="height: 100%; resize: none"
  />
</template>

<script>
export default {
  data() {
    return {
      text: ''
    }
  },
  created() {
    this.text = this.$root.appdata.notepad || ''
  },
  destroyed() {
    this.$root.appdata.notepad = this.text
  }
}
</script>